<template>
  <ul class="step-list">
    <li
      v-for="(item, index) in stepList"
      :key="index"
      :class="[
        activeIndex > index ? 'step-list-item_process' : '',
        activeIndex === index ? 'step-list-item_active' : '',
        'step-list-item',
      ]"
    >
      <span :data-value="index + 1">{{ item.label }}</span>
    </li>
  </ul>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { useVueRouter } from "@assets/useHook";

const stepList = [
  { label: "选择客户", value: ["orderManagement.createOrder.companyList"] },
  {
    label: "选择产品",
    value: [
      "orderManagement.createOrder.applicationCenter",
      "orderManagement.createOrder.productDetail",
    ],
  },
  { label: "确认订单", value: ["orderManagement.createOrder.confirmOrder"] },
  { label: "订单完成", value: [] },
];
const { route } = useVueRouter();
const activeIndex = computed(() => {
  return stepList.findIndex((item) => {
    return item.value.includes(route.name);
  });
});
</script>

<style scoped lang="scss">
.step-list {
  @include flex();
  height: 60px;
  border-radius: 4px;
  background-color: #ffffff;
  &-item {
    flex: 1;
    @include flex(center, center);
    background: url("@/assets/images/progress_arrow.png") center right no-repeat;
    background-size: 22px 68px;
    span {
      @include flex(center);
      cursor: pointer;
      color: #242934;
      font-size: 14px;
      &::before {
        @include flex(center, center);
        content: attr(data-value);
        width: 20px;
        height: 20px;
        margin-right: 8px;
        vertical-align: middle;
        border-radius: 100%;
        border: 1px solid #e1e4e8;
      }
    }
  }
  &-item_process {
    span {
      color: #0056ff;
      &::before {
        border-color: #0056ff;
      }
    }
  }
  &-item_active {
    span {
      color: #0056ff;
      font-weight: 900;
      &::before {
        font-weight: initial;
        border-color: #0056ff;
      }
    }
  }
  &-item:last-child {
    background: none;
  }
}
</style>
